import { optionInfo } from "@/composables/baseTypes";
import { Ref, ref, reactive, onMounted } from "vue";
import { PicForm } from "@/composables/baseTypes";

export default function setupForm() {
    //
    const option: Ref<optionInfo> = ref({
      img: "", // 
      info: true, // 
      outputSize: 0.8, // 
      outputType: "jpeg", // 
      canScale: true, // 
      autoCrop: true, // 
      autoCropWidth: 224, // ,,
      autoCropHeight: 224, // ,,
      fixedBox: true, // ,
      fixed: true, // 
      fixedNumber: [1, 1], // 
      full: false, // 
      canMove: true, // 
      canMoveBox: true, // 
      original: true, // 
      centerBox: true, // 
      high: false, // dpr,
      infoTrue: false, // true  false 
      maxImgSize: 3000, //
      enlarge: 0.8, //,
      mode: "100%", //,,contain,100%
    });
    const picForm = reactive(new PicForm(22.5366, 113.9343, [new Date(), new Date()], 'other', 'medium', 0, ['', '', ''], 4, 'north', 0, 23, 40, 1, 0, ''));
  
    return {
      option,
      picForm
    }
  }
  
// 
export const shortcuts = [
  {
    text: "",
    value: () => {
      let today = new Date();
      let day = today.getDay() || 7; // Date.getDay()0 7
      return [
        new Date(
          today.getFullYear(),
          today.getMonth(),
          today.getDate() + 1 - day
        ),
        new Date(),
      ];
    },
  },
  {
    text: "",
    value: () => {
      const end = new Date();
      const start = new Date(
        new Date().getFullYear(),
        new Date().getMonth(),
        1
      );
      return [start, end];
    },
  },
  {
    text: "",
    value: () => {
      const end = new Date();
      const start = new Date(new Date().getFullYear(), 0);
      return [start, end];
    },
  },
  {
    text: "",
    value: () => {
      return [
        new Date().setTime(new Date().getTime() - 3600 * 1000 * 24),
        new Date(),
      ];
    },
  },
  {
    text: "",
    value: () => {
      return [
        new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 7),
        new Date(),
      ];
    },
  },
  {
    text: "",
    value: () => {
      const end = new Date();
      const start = new Date();
      start.setMonth(start.getMonth() - 1);
      return [start, end];
    },
  },
  {
    text: "",
    value: () => {
      const end = new Date();
      const start = new Date();
      start.setMonth(start.getMonth() - 3);
      return [start, end];
    },
  },
];

