import { ElMessage } from "element-plus";
import { Ref } from "vue";
import axios from "axios";
import { router } from "@/router";
import {
  openErrorMessage,
  openSuccessMessage,
  openWarningMessage,
} from "@/composables/utilsFunction";

/**
 * 
 * @param poiDetailsList
 */
export default function postForm(
  formData: FormData,
  isSubmittingQueryForm: Ref<boolean>
) {
  // -----  -----
  ElMessage.info("");
  const controller = new AbortController();
  // 
  isSubmittingQueryForm.value = true; // 

  // RESTful API
  const waitTimeOut = setTimeout(() => {
    openWarningMessage("");
  }, 12000);
  const stopQueryTimeOut = setTimeout(() => {
    openErrorMessage(
      ""
    );
    isSubmittingQueryForm.value = false;
    controller.abort(); // axios
  }, 45000);

  axios
    .post("/addDatabase", formData, {
      signal: controller.signal,
    }) // 
    .then((response) => {
      // response
      return response.data;
    })
    .then((data) => {
      if (data.message === "") {
        openSuccessMessage("");
      } else {
        openWarningMessage("");
      }

    //   console.log(data);
      const params = { resultdata: data };
      router
        .push({
          // 
          name: "Mapbox",
          // params: {
          //     resultdata: data,
          // },
          state: { params },
        })
        .then(() => clear(waitTimeOut, stopQueryTimeOut, isSubmittingQueryForm))
        .catch(
          // 
          (error) => {
            clear(waitTimeOut, stopQueryTimeOut, isSubmittingQueryForm);
            throw Error(
              `${String(
                error
              ).substr(0, 100)}`
            );
          }
        );
    })
    .catch(
      // 
      (error: Error) => {
        clear(
          waitTimeOut,
          stopQueryTimeOut,
          isSubmittingQueryForm,
          error.message
        );
      }
    );
}

/**
 * timeout
 * @param waitTimeOut
 * @param stopQueryTimeOut
 * @param isSubmittingQueryForm
 * @param errorMessage
 */
function clear(
  waitTimeOut: number,
  stopQueryTimeOut: number,
  isSubmittingQueryForm: Ref<boolean>,
  errorMessage: string = ""
) {
  clearTimeout(waitTimeOut);
  clearTimeout(stopQueryTimeOut);
  isSubmittingQueryForm.value = false;

  if (errorMessage != "") {
    openErrorMessage(errorMessage, "long");
  }
}
